import React, { Component } from 'react';
import styled from 'styled-components';
import ImageArrow from './ImageArrow';

const ProjectDetailedContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
  width: 100%;
  /* max-width: 1600px; */
  /* margin: 0 auto 5rem auto; */
  margin: 0 0 5rem 0;

  background-color: rgb(250, 250, 250);
  @media only screen and (max-width: 685px) {
    flex-direction: column;
  }
  border-top: 2px solid rgb(255, 50, 50, 0.9);
  border-bottom: 2px solid rgb(255, 50, 50, 0.9);
`;

const ProjectDetailedDescription = styled.div`
  background-color: rgb(250, 250, 250);
  flex: 1;
  display: flex;
  margin-top: 2rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  padding: 0 2.5rem;
  width: 100%;
  height: 41rem;
  font-size: 2.1rem;
  font-family: 'Play', sans-serif;
  color: rgb(25, 25, 25);

  @media only screen and (max-width: 1250px) {
    height: 44.8rem;
  }

  @media only screen and (max-width: 835px) {
    height: 48rem;
  }

  @media only screen and (max-width: 685px) {
    height: 38rem;
  }

  @media only screen and (max-width: 510px) {
  }

  @media only screen and (max-width: 445px) {
  }

  @media only screen and (max-width: 425px) {
    height: 37.5rem;
  }
`;

const ProjectLogosContainer = styled.div`
  margin-top: 3rem;

  & svg {
    width: 60px;
    height: 60px;

    @media only screen and (max-width: 1250px) {
      width: 55px;
      height: 55px;
    }

    @media only screen and (max-width: 835px) {
      width: 50px;
      height: 50px;
    }

    @media only screen and (max-width: 685px) {
      width: 45px;
      height: 45px;
    }

    @media only screen and (max-width: 445px) {
      width: 40px;
      height: 40px;
    }
  }
`;

const ProjectImages = styled.img`
  position: relative;
  width: 100%;
  max-height: 435px;
  user-select: none;
`;

const ProjectProImages = styled.img`
  position: relative;
  width: 100%;
  max-height: 600px;
  user-select: none;
`;

const ProjectImagesContainer = styled.div`
  flex: 1;
  width: 100%;
  max-width: 900px;
  background-color: rgb(250, 250, 250);
`;

class ProjectVerticalDetailed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      maxIndex: props.project.images.length - 1,
      currentIndex: 0,
    };

    this.handleArrowClick = this.handleArrowClick.bind(this);
  }

  handleArrowClick(direction) {
    let newIndex;

    if (direction === 'left') {
      if (this.state.currentIndex === 0) {
        newIndex = this.state.maxIndex;
      } else {
        newIndex = this.state.currentIndex - 1;
      }
    } else if (direction === 'right') {
      if (this.state.currentIndex === this.state.maxIndex) {
        newIndex = 0;
      } else {
        newIndex = this.state.currentIndex + 1;
      }
    }

    this.setState({ currentIndex: newIndex });
  }

  render() {
    const DescriptionText = (
      <div
        style={{
          margin: '0px auto',
          maxWidth: '650px',
        }}
      >
        {this.props.project.description[this.props.language]}
      </div>
    );
    const Logos = <>{this.props.project.logos}</>;
    const Image = (
      <ProjectImagesContainer>
        <h4
          style={{
            fontFamily: 'Play, sans-serif',
            color: 'rgb(25,25,25)',
            fontSize: '4rem',
          }}
        >
          {this.props.project.title}
        </h4>
        <br /> <br />
        {this.props.project.pro ? (
          <ProjectProImages
            src={this.props.project.images[this.state.currentIndex]}
            alt="project image"
          />
        ) : (
          <ProjectImages
            src={this.props.project.images[this.state.currentIndex]}
            alt="project image"
          />
        )}
        <div>
          <ImageArrow
            direction="left"
            handleArrowClick={this.handleArrowClick}
          />
          <ImageArrow
            direction="right"
            handleArrowClick={this.handleArrowClick}
          />
        </div>
        <br />
      </ProjectImagesContainer>
    );

    return (
      <ProjectDetailedContainer>
        {Image}
        <ProjectDetailedDescription>
          {DescriptionText}
          <ProjectLogosContainer>{Logos}</ProjectLogosContainer>
        </ProjectDetailedDescription>
      </ProjectDetailedContainer>
    );
  }
}

export default ProjectVerticalDetailed;
