const ProjectsVertical = props => {
  // let ExtraInfo;
  // let HeaderPersonalProjects;
  let HeaderProProjects;

  if (props.language === 'spa') {
    HeaderProProjects = <h3>Proyectos Profesionales</h3>;
    // HeaderPersonalProjects = <h3>Proyectos Personales</h3>;
    // ExtraInfo = (
    //   <ExtraInfoText>
    //     Encuentra más de mis proyectos en mi{' '}
    //     <a
    //       rel="noopener noreferrer"
    //       target="_blank"
    //       href="https://github.com/Javier-Machin"
    //     >
    //       Github
    //     </a>
    //     .
    //   </ExtraInfoText>
    // );
  } else {
    HeaderProProjects = <h3>Professional Projects</h3>;
    // HeaderPersonalProjects = <h3>Personal Projects</h3>;
    // ExtraInfo = (
    //   <ExtraInfoText>
    //     Find more of my projects in my{' '}
    //     <a
    //       rel="noopener noreferrer"
    //       target="_blank"
    //       href="https://github.com/Javier-Machin"
    //     >
    //       Github
    //     </a>
    //     .
    //   </ExtraInfoText>
    // );
  }

  const renderProjects = (pro = false) => {
    return Object.values(projects)
      .filter(project => project.pro === pro)
      .map((project, index) => {
        return (
          <ProjectVerticalDetailed
            key={index}
            project={project}
            language={props.language}
          />
        );
      });
  };

  return (
    <ProjectsContainer id="projects">
      {HeaderProProjects}
      {renderProjects(true)}
    </ProjectsContainer>
  );
};

export default ProjectsVertical;
