let docHeight;
let aboutOffset;
let projectstOffset;
let opensourceOffset;
let contactOffset;

function debounce(func, wait, immediate) {
  let timeout;
  return function() {
    let context = this 
    let args = arguments;
    let later = function() {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };
    let callNow = immediate && !timeout;
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) func.apply(context, args);
  };
};

function updateOffsets() { 
  aboutOffset = document.getElementById("about").offsetTop;
  projectstOffset = document.getElementById("projects").offsetTop;
  opensourceOffset = document.getElementById("opensource").offsetTop;
  contactOffset = document.getElementById("contact").offsetTop;
};

function handleScroll2() {
  let state = this.state;

  if (docHeight !== document.documentElement.scrollHeight) {
    docHeight = document.documentElement.scrollHeight; 
    updateOffsets();
  };
  
  // Sticky Navbar JS Implementation

  // if (window.pageYOffset >= aboutOffset - 50) {
  //   state.navPosition = "fixed";
  //   state.aboutHeight = "65rem";
  //   state.aboutPadding = "15rem 5rem 10rem 5rem";
  // } else {
  //   state.navPosition = "relative";
  //   state.aboutHeight = "60rem";
  //   state.aboutPadding = "10rem 5rem 10rem 5rem";
  // }

  if (window.pageYOffset <= aboutOffset - 400) {
    state.styles.home = {color: "rgba(255,50,50,.9)"};
    state.styles.about = {};
    state.styles.projects = {};
    state.styles.opensource = {};
    state.styles.contact = {};
  } else if (window.pageYOffset >= contactOffset - 420) {
    state.styles.home = {};
    state.styles.about = {};
    state.styles.projects = {};
    state.styles.opensource = {};
    state.styles.contact = {color: "rgba(255,50,50,.9)"};
  } else if (window.pageYOffset >= opensourceOffset - 420) {
    state.styles.home = {};
    state.styles.about = {};
    state.styles.projects = {};
    state.styles.opensource = {color: "rgba(255,50,50,.9)"};
    state.styles.contact = {};
  } else if (window.pageYOffset >= projectstOffset - 420) {
    state.styles.home = {};
    state.styles.about = {};
    state.styles.projects = {color: "rgba(255,50,50,.9)"};
    state.styles.opensource = {};
    state.styles.contact = {};
  } else if (window.pageYOffset >= aboutOffset - 420) {
    state.styles.home = {};
    state.styles.about = {color: "rgba(255,50,50,.9)"};
    state.styles.projects = {};
    state.styles.opensource = {};
    state.styles.contact = {};
  }

  this.setState(state);
}

const handleScroll = debounce(handleScroll2, 60);

export { handleScroll, updateOffsets }